import { Model } from './base/Model';
export class ProductItem extends Model {
}
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            payment: 'online',
            address: '',
            email: '',
            total: 0,
            phone: '',
            items: [],
        };
        this.formOrderErrors = {};
        this.formContactsErrors = {};
    }
    getTotal() {
        return this.order.items.reduce((ans, item) => ans + this.gallery.find((it) => it.id === item).price, 0);
    }
    setGallery(products) {
        console.log(products);
        this.gallery = products.items.map((product) => new ProductItem(product, this.events));
        this.emitChanges('items:changed', { gallery: this.gallery });
    }
    addProduct(item) {
        this.basket.push(item);
    }
    deleteProduct(id) {
        this.basket = this.basket.filter((item, i) => {
            return i + 1 !== id;
        });
        this.events.emit('basket:open');
    }
    clearBasket() {
        this.basket = [];
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    setContactsField(field, value) {
        this.order[field] = value;
        console.log(field, value);
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        this.formOrderErrors = errors;
        this.events.emit('formOrderErrors:change', this.formOrderErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        console.log(this.order);
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        if (!this.order.email) {
            errors.email = 'Необходимо указать почту';
        }
        this.formContactsErrors = errors;
        this.events.emit('formContactsErrors:change', this.formContactsErrors);
        return Object.keys(errors).length === 0;
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    hasInBasket(id) {
        return this.basket.find((item) => item.id === id) !== undefined;
    }
}
