import { Api } from './base/api';
export class LarekAPI extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getProductList() {
        return this.get(`/product`).then((products) => (Object.assign({}, products)));
    }
    getProduct(id) {
        return this.get(`/product/${id}`).then((product) => product);
    }
    makeOrder(order) {
        return this.post(`/order`, order).then((res) => res);
    }
}
