import { Form } from './common/Form';
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
    set email(val) {
        this.container.elements.namedItem('email').value = val;
    }
    set phone(val) {
        this.container.elements.namedItem('phone').value = val;
    }
}
